import { createSlice } from '@reduxjs/toolkit'

export const fetchWeatherSlice = createSlice({
  name: 'fetchWeather',
  initialState: {
    value: {},
    hasWeatherData: false
  },
  reducers: {
    fetchWeather: (state, action) => {
        const data = fetch(`https://www.el-tiempo.net/api/json/v2/provincias/${action.payload}`)
        .then(response => response.json)

        
    
        let hasData = false;

        if(data.title && data.today && data.tomorrow && data.ciudades && data.ciudades.length) {
            hasData = true
        }
    
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      /* console.log('state.value', state.value)
      return !state.value || !state */

      return {
        ...state,
        value: data,
        hasWeatherData: hasData
      }
      
    },
  },
})

// Action creators are generated for each case reducer function
export const { fetchWeather } = fetchWeatherSlice.actions

export default fetchWeatherSlice.reducer